import * as express from 'express'

let request = require('superagent');
let CronJob = require('cron').CronJob;
let _ = require('underscore');

export abstract class Api_Module {
    static serve() {
        new CronJob(
            '0 */10 * * * *',
            () => {
                let now = new Date();
                if (now.getHours() > 10 && now.getHours() < 19) {
                    request.get("https://dbcron.herokuapp.com");
                }
            }, () => {
                console.log("Monitor Ended!!");
            },
            true,
            'Asia/Kolkata',
            null,
            true
        );
        let port = process.env.PORT || 3002;
        let app: express.Application = express();
        app.get('/', (req, res) => res.send('Monitor Job is up!'));
        app.listen(port, function() {
            console.log('Monitor started on ' + port);
        });
    }
}